/* jshint ignore:start */

define('santuary-computer/config/environment', ['ember'], function(Ember) {
  var prefix = 'santuary-computer';
/* jshint ignore:start */

try {
  var metaName = prefix + '/config/environment';
  var rawConfig = Ember['default'].$('meta[name="' + metaName + '"]').attr('content');
  var config = JSON.parse(unescape(rawConfig));

  return { 'default': config };
}
catch(err) {
  throw new Error('Could not read config from meta tag with name "' + metaName + '".');
}

/* jshint ignore:end */

});

if (runningTests) {
  require("santuary-computer/tests/test-helper");
} else {
  require("santuary-computer/app")["default"].create({"name":"santuary-computer","version":"0.0.0.195eb7b2"});
}

/* jshint ignore:end */
